/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable import/no-anonymous-default-export */
import React from 'react'
import './Card.css'

export default props =>
    <div className="Card">
        <div className="Image">
            <img className="foto" src={props.src} />
        </div>
        <div className="Descricao">
            <div className="Cima">{props.titulo}</div>
            <div className="Baixo">
                <div className="Valor"><b>R${props.valor}</b></div>
                <div className="Ajudar">
                    
                <a href="https://nubank.com.br/pagar/5h20p/1BBPejYTjj">CLICK</a>
                </div>
            </div>
        </div>
    </div>