import React from 'react'
import produtos from '../data/produtos'
import Card from './layout/Card'

export default props => {
    
    function getProdutos() {
        return produtos.map(prod =>{
            return <Card 
            titulo={prod.titulo}
            src={prod.src}
            valor={prod.valor}/>
        })
    }

    return(
        <div>
            <ul>
                {getProdutos()}
            </ul>
        </div>
    )
}